import "../styles/collectData.css";
import { connect } from "react-redux";
import { AddToList, DeleteFromList } from "../redux/action/data.action";
import { IncCardCount, DecCardCount } from "../redux/action/cards.action";

const CollectData = (props) => {

    const handleEnterKey = e => {
        if (e.charCode == 13) {
            props.AddToList(e.currentTarget.value);
        }
    };

    const handleDelete = () => {
        props.DeleteFromList();
    };

    const handleCardEnterKey = e => {
        if (e.charCode == 13) {
            props.IncCardCount(e.currentTarget.value);
        }
    };

    const handleCardDelete = () => {
        props.DecCardCount();
    };

    return (
        <div>
            <div className="data">
                <h3>Project To ADD NUMBERS OR COLORS</h3>
            </div>
            <label>Add Number</label>
            <input
                type="text"
                onKeyPress={handleEnterKey}>
            </input>
            <button onClick={() => handleDelete()}>Delete Last Item</button>
            <ul>NUMBERS IN LIST:
        {props.state.list.map(item => {
                return <li key={item}>{item}</li>;
            })}
            </ul>

            <label>Add Card Count</label>
            <input
                type="text"
                onKeyPress={handleCardEnterKey}>
            </input>
            <button onClick={() => handleCardDelete()}>Delete Last Item</button>
            <ul>NUMBERS IN LIST:
        {props.state.cardCount.map(item => {
                return <li key={item}>{item}</li>;
            })}
            </ul>
        </div>
    );
};



const mapStateToProps = state => {
    console.log("State of collectData ", state);
    return {

        state: state.ListReducer
    };
};

const mapDispatchToProps = data => {
    console.log("data ", data);
    return {};
};
// 

export default connect(mapStateToProps, { AddToList, DeleteFromList, IncCardCount, DecCardCount })(CollectData);

